import React, { useState, useEffect } from 'react';
import { useNavigate, useParams } from "react-router-dom";

import styles from './Course_check.module.css';
import { Course_delete, Course_content, Course_info } from '.';

const Course_check = () => {
    console.log("Course_check() invoked.");

    const navigate = useNavigate();
    //    ///////////////////////////////////////////////////////////////////////////////////////
    const { courseId } = useParams(); //   

    const [formData, setFormData] = useState({
        type: "",
        name: "",
        startDate: "",
        endDate: "",
        capacity: "",
        currCount: "",
        detail: "",
        instructor: "",
        upfilePath: "",
        upfileUuid: "",
        upfileExtension: "",
        upfileOriginal: "",
    });
    const [trainees, setTrainees] = useState([]);
    const [status, setStatus] = useState("");

    //   
    function formatPhoneNumber(tel) {
        const telStr = tel.toString();
        return `${telStr.slice(0, 3)}-${telStr.slice(3, 7)}-${telStr.slice(7, 11)}`;
    }
    useEffect(() => {
        console.log(" formData:", formData);
    }, [formData]);  //  formData    

    useEffect(() => {                               //    (   )
        console.log("courseId:", courseId);
        fetch(`https://localhost:443/course/${courseId}`)
            .then((response) => {
                if (!response.ok) {
                    throw new Error("  ");
                }
                return response.json();
            })
            .then((data) => {
                console.log("curr", data.currCount);
                console.log("status", data.status);
                setFormData({
                    type: data?.type || "",
                    name: data?.name || "",
                    startDate: data?.startDate || "",
                    endDate: data?.endDate || "",
                    currCount: data?.currCount || "0",
                    capacity: data?.capacity || "",
                    detail: data?.detail || "",
                    instructor: data.instructor?.name || "  ",
                    upfilePath: data.upfile[0]?.path || "",
                    upfileUuid: data.upfile[0]?.uuid || "",
                    upfileExtension: data.upfile[0]?.extension || "",
                    upfileOriginal: data.upfile[0]?.original || "",
                });
                setStatus(data?.status || "");

                const formattedTrainees = data.trainees.map((member) => ({
                    ...member,
                    tel: formatPhoneNumber(member.tel),
                }));

                setTrainees(formattedTrainees);

                console.log("data", data);
                console.log("status", data.status);
            })
            .catch((error) => {
                console.error("  :", error);
            });
        // fetchData();    // *** db   ***! 
    }, []);

    ////  ////////////////////////////////////////////////////////////////////////////////////////////////

    const handleModifyShowPopup = () => {
        navigate(`/course/update/${courseId}`);
    };


    ////  ////////////////////////////////////////////////////////////////////////////////////////////////

    const [showDeletePopup, setShowDeletePopup] = useState(false);  //  


    //      
    const handleDeleteShowPopup = () => {
        setShowDeletePopup(true);
    };

    //  
    const handleDeleteClosePopup = () => {
        setShowDeletePopup(false);
    };

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    const typeMapping = {
        1: "NCS",
        2: "KDT",
        3: "",
        4: ""
    };

    const statusMap = {
        "1": "",
        "2": "",
        "3": "",
        "4": "",
    };

    return (

        <div className={styles.main}>

            {showDeletePopup && <Course_delete onClose={handleDeleteClosePopup} />}

            <div className={styles.topbox}>

                <div className={styles.top_leftbox}>

                    <button className={styles.backbutton} onClick={() => navigate(`/course_overview/${status}`)}><i class="fa-solid fa-chevron-left fa-1.8x"></i></button> {/*   */}
                    {/* <div className={styles.imgbox} > <img src={`/courseFile/39241600-2977-4137-bd11-3fd968422601.png`}></img></div> */}
                    {/* <div className={styles.imgbox} > <img src={`${formData.upfilePath}${formData.upfileUuid}${formData.upfileExtension}`}></img></div> */}
                    <div className={styles.imgbox} > <img src={`${formData.upfilePath}${formData.upfileOriginal}`}></img></div>
                    <div className={styles.buttonbox}>
                        <button className={styles.modify_button} onClick={handleModifyShowPopup}></button>
                        <button className={styles.delete_button} onClick={handleDeleteShowPopup}></button>
                    </div>

                </div>

                <div className={styles.top_rightbox}>
                    <div className={styles.inputboxes}>
                        <Course_info label="" value={typeMapping[formData.type] || ""} disabled />
                        <Course_info label="" value={formData.startDate} disabled />
                        <Course_info label="" value={formData.name} disabled />
                        <Course_info label="" value={formData.endDate} disabled />
                        <Course_info label="" value={formData.instructor} disabled />
                        <Course_info label={` ( : ${formData.currCount})`} value={formData.capacity} disabled />
                    </div>
                    <Course_content label="" value={formData.detail} disabled />
                </div>


            </div>

            <div className={styles.bottombox}>
                <div className={styles.listbox}>
                    <div className={styles.tableHeader}>
                        <div></div>
                        <div></div>
                        <div></div>
                        <div></div>
                        <div></div>
                    </div>
                    <table className={styles.tableBody}>
                        <tbody>
                            {trainees.map((member) => (
                                <tr key={member.traineeId} className={styles.tr}>
                                    <td>{member.traineeId}</td>
                                    <td>{member.name}</td>
                                    <td>{member.tel}</td>
                                    <td>{member.crtDate}</td>
                                    <td>{statusMap[member.status]}</td>
                                </tr>
                            ))}
                        </tbody>
                    </table>
                </div>
            </div>
        </div>
    )

}


export default Course_check;