import React, { useState, useEffect } from 'react';
import styles from './Instructor_registration.module.css';

const Instructor_registration = ({ onClose }) => {

  const reloadPage = () => {
    window.location.reload(); //   
  };

  const [courses, setCourses] = useState([]);
  const [registerForm, setRegisterForm] = useState({
    name: "",
    tel: "",
    courseId: "",
  });

  const [registerFile, setRegisterFile] = useState(null);

  const handleChange = (e) => {
    setRegisterForm({ ...registerForm, [e.target.name]: e.target.value });
  };
  
  const handleFileChange = (event) => {
    const file = event.target.files[0];
    setRegisterFile(file);
  };

  const handleCourseChange = (event) => {
    const selectedValue = event.target.value;
    setRegisterForm((prev) => ({
      ...prev,
      courseId: selectedValue,
    }));
  };

  useEffect(() => {
    const fetchCourses = async () => {
      try {
        const response = await fetch('https://localhost:443/course/selectCourseIns', { method: 'GET' });
        if (response.ok) {
          const data = await response.json();
          setCourses(data);
          console.log('data: ', data);
        } else {
          console.log('   .');
        }
      } catch (error) {
      }
    };
    fetchCourses();
  }, []);

  // useEffect(() => {
  //   if (courses.length > 0 && !registerForm.courseId) {
  //     setRegisterForm((prevForm) => ({
  //       ...prevForm,
  //       courseId: courses[0].courseId,
  //     }));
  //   }
  // }, [courses]);

  const handleSubmit = async (event) => {
    event.preventDefault();

    if (!registerForm.courseId || !registerForm.name || !registerForm.tel) {
      alert(', ,  ');
      return;
    }

    console.log('registerForm: ', registerForm);
    console.log('registerFile: ', registerFile);

    try {
      const formData = new FormData();
      formData.append("name", registerForm.name);
      formData.append("courseId", registerForm.courseId);
      formData.append("tel", registerForm.tel);
      if (registerFile) formData.append("upfiles", registerFile);

      const response = await fetch('https://localhost:443/instructor', { 
        method: "PUT", 
        body: formData,
      });

      if (response.ok) {
        alert('  .');
        console.log(response);
        reloadPage();
        onClose();
      } else {
        alert('  .');
        console.log(response);
      }
    } catch (error) {
      alert(' .  .');
    }
  };

  const handleCancelClick = () => {
    if (window.confirm(' ?')) {
      setRegisterForm({ name: "", tel: "", courseId: "" });
      setRegisterFile(null);
      onClose();
    }
  };

  return (
    <div className={styles.registration_background}>
      <div className={styles.registration_container}>
        <h2> </h2>
        <form onSubmit={handleSubmit}>
          <div className={styles.registration_form}>
            <input
              className={styles.registration_textbox}
              name="name"
              placeholder=""
              value={registerForm.name}
              onChange={handleChange}
            />
            <input
              className={styles.registration_textbox}
              name="tel"
              placeholder="(-)"
              value={registerForm.tel}
              onChange={handleChange}
              maxLength={11}
            />
            <select
              name="courseId"
              className={styles.registration_textbox}
              value={registerForm.courseId}
              onChange={handleCourseChange}
            >
              <option value=""> </option>
              {courses.map((course) => (
                <option key={course.courseId} value={course.courseId}>
                  {course.name}
                </option>
              ))}
            </select>

            <div className={styles.registration_photo}>
              <input type="file" name="file" onChange={handleFileChange} />
            </div>
            <div className={styles.registration_btns}>
              <button type="submit" className={styles.registration_submit}></button>
              <button type="button" className={styles.registration_cancel} onClick={handleCancelClick}></button>
            </div>
          </div>
        </form>
      </div>
    </div>
  );
};

export default Instructor_registration;
