import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';

import styles from './Login.module.css';

const Login = () => {
    console.log("Login() invoked.");

    const navigate = useNavigate();
    const [message, setMessage] = useState();

    //  :    
    const [loginData, setLoginData] = useState({
        userId: '',
        passwd: ''
    });

    //   
    const handleInputChange = (e) => {
        const { name, value } = e.target;
        setLoginData((prevData) => ({
            ...prevData,
            [name]: value
        }));
    };

    //        
    const handleLogin = async () => {

        //   
        try {
            const formData = new FormData();
            formData.append("userId", loginData.userId);
            formData.append("passwd", loginData.passwd);

            const response = await fetch('https://localhost:443/security/login', {
                method: 'POST',
                body: formData,
            });

            const text = await response.text();
            console.log(" :", text);

            if (response.ok) {
                //    
                console.log(" ");
                setMessage(text);
                navigate("/course_overview/2");

            } else {
                //    
                console.log(" ");
                setMessage(text);
            }
        } catch (error) {
            console.error(" :", error);
        }
    };

    //      
    const handleKeyPress = (e) => {
        if (e.key === 'Enter') {
            handleLogin();
        }
    };


    return (
        <div className={styles.login}>
            <div className={styles.main}>
                <div className={styles.logo}></div>
                <div className={styles.id_container}>
                    <input
                        className={styles.id}
                        placeholder="userId"
                        name="userId"                           //   
                        value={loginData.userId}
                        onChange={handleInputChange}
                        onKeyUp={handleKeyPress}
                    />
                    <i className={`fas fa-user ${styles.user_icon}`} />
                </div>
                <div className={styles.pw_container}>
                    <input
                        className={styles.pw}
                        placeholder="password"
                        name="passwd"                     //   
                        type="password"
                        value={loginData.passwd}
                        onChange={handleInputChange}
                        onKeyUp={handleKeyPress}
                    ></input>
                    <i className={`fas fa-lock ${styles.pass_icon}`} />
                </div>

                {message && (
                    <div className={styles.message}>
                        {message}
                    </div>
                )}
                <div className={styles.button}>
                    <button className={styles.loginbutton} onClick={handleLogin}>
                        
                    </button>
                </div>
            </div>
        </div>
    )
}

export default Login;