import { Link } from 'react-router-dom';
import React from 'react'
import styles from './Navbar.module.css';

const Navbar = () => {

  const handleLogout = () => {
    fetch('https://localhost:443/security/logout', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
    })
      .then((response) => {
        if (!response.ok) {
          throw new Error(' ');
        }
        return response.json();
      })
      .then((data) => {
        if (data.message === ' ') {
          alert(" .")
          window.location.href = '/';
        } else {
          console.error(' :', data.message);
        }
      })
      .catch((error) => {
        console.error('   :', error);
      });
  };

  return (
    <div className={styles.nav}>

      <div className={styles.container}>
        <div className={styles.title}> </div>
        <div className={styles.content}><Link to="/course_overview/1" ></Link></div>
        <div className={styles.content}><Link to="/course_overview/2" ></Link></div>
        <div className={styles.content}><Link to="/course_overview/4" ></Link></div>

        <br />

        <div className={styles.title}> </div>
        <div className={styles.content}><Link to="/trainee_list"  ></Link></div>
        <div className={styles.content}><Link to="/instructor_list" ></Link></div>

        <button className={styles.logout} onClick={handleLogout} >Logout</button>
      </div>

    </div>
  )
}

export default Navbar;