import React, { useState, useEffect } from 'react';
import { useNavigate, useParams } from 'react-router-dom';
import styles from './Trainee_modification.module.css';

const Trainee_modification = () => {
  const navigate = useNavigate();
  const { traineeId } = useParams();

  const [courses, setCourses] = useState([]);
  const [registerForm, setRegisterForm] = useState({
    name: '',
    tel: '',
    course: '',
    status: '',
    enabled: '',
    upfiles: null,
    existingFileName: '', //    
  });

  //    
  useEffect(() => {
    const fetchTraineeData = async () => {


      try {
        const response = await fetch(`https://localhost:443/trainee/${traineeId}`);

        if (response.ok) {
          const data = await response.json();
          setRegisterForm({
            name: data.name,
            tel: data.tel,
            course: data.course ? data.course.courseId : '', // course    
            status: data.status,
            enabled: data.enabled,
            upfiles: null, //     
            existingFileName: data.upfile[0]?.original || '  ', //    
          });
          console.log(' :', data);
        } else {
          console.error('   :', response.statusText);
        }
      } catch (error) {
        console.error('   :', error);
      }
    };

    fetchTraineeData();
  }, [traineeId]);


  //   
  useEffect(() => {
    const fetchCourses = async () => {


      try {
        const response = await fetch(`https://localhost:443/course/selectCourseTrn`,
          {
            method: 'GET',
          });

        if (response.ok) {
          const data = await response.json();
          setCourses(data);
          console.log(' :', data);
        } else {
          console.error('Failed to fetch courses:', response.statusText);
        }
      } catch (error) {
        console.error('Error fetching courses:', error);
      }
    };

    fetchCourses();
  }, []);

  //   
  useEffect(() => {
    console.log(' :', registerForm);
    console.log(' :', courses);
    console.log(' :', registerForm.upfiles);
  }, []);

  //   
  const handleChange = (field, value) => {
    setRegisterForm((prevData) => ({
      ...prevData,
      [field]: value,
    }));
  };

  //   
  const handleFileChange = (event) => {
    const file = event.target.files[0];
    setRegisterForm((prevForm) => ({
      ...prevForm,
      upfiles: file,
      existingFileName: file ? file.name : prevForm.existingFileName, //    
    }));
  };

  //   
  const handleSubmit = async (event) => {
    event.preventDefault();

    try {
      const formData = new FormData();
      formData.append('name', registerForm.name);
      formData.append('tel', registerForm.tel);
      formData.append('courseId', registerForm.course);
      formData.append('status', registerForm.status);
      formData.append('enabled', registerForm.enabled);

      if (registerForm.upfiles) {
        formData.append('upfiles', registerForm.upfiles); //   
      }

      const response = await fetch(`https://localhost:443/trainee/${traineeId}`, {
        method: 'POST', //   PUT  
        body: formData,
      });

      if (response.ok) {
        alert('   .');
        navigate('/trainee_list'); //   
      } else {
        console.error(' :', response.statusText);
        alert('   .');
      }
    } catch (error) {
      console.error('   :', error);
      alert(' .  .');
    }
  };

  //   
  const handleCancelClick = () => {
    navigate(-1); //   
  };

  //   
  const statusOption = {
    "1": "",
    "2": "",
    "3": "",
    "4": "",
  };

  return (
    <div className={styles.main}>
      <div className={styles.background} />
      <div className={styles.container}>
        <h2> </h2>
        <form onSubmit={handleSubmit}>
          <div className={styles.form}>
            <input
              type="text"
              className={styles.textbox}
              placeholder=""
              name="name"
              value={registerForm.name}
              onChange={(e) => handleChange(e.target.name, e.target.value)}
              required
            />

            <input
              type="tel"
              className={styles.textbox}
              placeholder="(-)"
              name="tel"
              value={registerForm.tel}
              onChange={(e) => handleChange(e.target.name, e.target.value)}
              maxLength={11}
              required
            />

            <select
              name="course"
              className={styles.textbox}
              value={registerForm.course || ''}
              onChange={(e) => handleChange(e.target.name, e.target.value)}
            >
              {courses.map((course) => (
                <option key={course.courseId} value={course.courseId}>
                  {course.name}
                </option>
              ))}
            </select>

            <select
              name="status"
              className={styles.textbox}
              value={registerForm.status || ''}
              onChange={(e) => handleChange(e.target.name, e.target.value)}
            >
              <option value=""> </option>
              {Object.entries(statusOption).map(([key, value]) => (
                <option key={key} value={key}>
                  {value}
                </option>
              ))}
            </select>

            <div className={styles.photo}>
              <input type="file" name="upfiles" onChange={handleFileChange} />
            </div>
            
            <p>  : {registerForm.existingFileName}</p>

            <div className={styles.btns}>
              <button type="submit" className={styles.submit}></button>
              <button type="button" className={styles.cancel} onClick={handleCancelClick}></button>
            </div>
          </div>
        </form>
      </div>
    </div>
  );
};

export default Trainee_modification;
