
import styles from './Course_delete.module.css';
import { useParams , useNavigate} from 'react-router-dom';

const Course_delete = ({onClose}) => {
    console.log("Course_delete() invoked.");

    const navigate = useNavigate();
    const { courseId } = useParams(); // URL courseId 

    //   
    const handleDeleteConfirm = async () => {
        // if (!selectedCourseId) return;
        
    try {
        const response = await fetch(`https://localhost:443/course/${courseId}`, {
        method: "DELETE",
        });

        if (response.ok) {
            alert(".");
            navigate("/course_overview/1");
        } else {
            alert(" : ");
        }
    } catch (error) {
        console.error(" :", error);
        // alert("   .");
    }

    onClose(); //  
    };

    return(
        <div className={styles.main}>
            <div className={styles.container}>
                <div className={styles.deletetext}>   ?</div>
                <div className={styles.buttonbox}>
                    <button onClick={handleDeleteConfirm} className={styles.yesnobutton}></button>
                    <button onClick={onClose} className={styles.yesnobutton}></button>
                </div>
            </div>
        </div>
    )

}

export default Course_delete;